* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 16px;
  min-height: 100%;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2d3c57;
}
.title {
  margin-bottom: 2em;
  text-align: center;
  color: #faebd7;
}
.password-form {
  padding: 30px;
  background-color: white;
  display: flex;
  flex-direction: column;
  gap: 30px;
  border-radius: 0.3rem;
  min-height: 280px;
  box-shadow: 0 4px 50px rgba(0, 0, 0, 0.4);
}

.password-text {
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 0.3rem;
  width: 75ch;
  min-height: 2.4rem;
  margin-bottom: 1rem;
}

.password-inputs {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.flex-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

label {
  color: rgba(0, 0, 0, 0.6);
}
.btn {
  padding: 0.85em;
  margin: 1rem 0;
  border-radius: 0.3rem;
  border: none;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
  background-color: rgb(45, 48, 243);
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn:active {
  transform: scale(0.95);
}

.btn:hover {
  background-color: rgb(14, 11, 192);
}
@media screen and (max-width: 768px) {
  .password-form {
    width: 420px;
  }
  .password-text {
    width: 44ch;
  }
}
